import React from 'react';
import Section from './section';
import Wather from './Wather';
const Appwather = ()=>{
    return(
        <>
        <Wather render={(clima,func,error,uv)=>(<Section clima={clima} func={func} error={error} uv={uv}/>)}/>
        </>
    );
}
export default Appwather;